/* Base styles */
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background-color: #121212;
  color: #e0e0e0;
}

.App {
  max-width: 800px;
  margin: auto;
  padding: 20px;
  text-align: center;
}

h1 {
  font-size: 2.4rem;
  font-weight: 600;
  letter-spacing: 1px;
}


/* Input */
.inputStyle {
  padding: 10px;
  font-size: 1rem;
  width: 90%;
  max-width: 400px;
  border-radius: 8px;
  border: 1px solid #333;
  margin-bottom: 10px;
  box-sizing: border-box;
  background-color: #1e1e1e;
  color: #e0e0e0;
}

/* Buttons */
.buttonStyle {
  padding: 10px 10px;
  font-size: 1rem;
  cursor: pointer;
  margin: 5px;
  border-radius: 8px;
  border: none;
  background-color: #4F46E5;
  color: white;
  font-weight: 500;
  letter-spacing: 0.5px;
  text-transform: capitalize;
  transition: background 0.3s ease;
}

.buttonStyle:hover {
  background-color: #6366f1;
}

/* Task Info Box */
.info {
  border: 1px solid #2a2a2a;
  padding: 15px;
  margin: 10px auto;
  border-radius: 10px;
  background-color: #1e1e1e;
  text-align: left;
  box-shadow: 0 2px 8px rgba(255, 255, 255, 0.05);
  width: 90%;
  max-width: 600px;
  word-break: break-word;
}

.info-line {
  margin: 5px 0;
  font-size: 0.95rem;
  color: #e0e0e0;
}

.label {
  font-weight: bold;
  color: #ffffff;
}

.logoImage {
  width: 80px;
  display: block;
  margin: 0 auto 20px;
  filter: invert(1) brightness(1.2);
}

/* Responsive Typography */
@media (max-width: 600px) {
  h1 {
    font-size: 1.6rem;
    color: #ffffff;
  }

  .buttonStyle {
    font-size: 0.95rem;
    padding: 8px 16px;
  }

  .info-line {
    font-size: 0.9rem;
    color: #e0e0e0;
  }
}